.signup-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 32px;
    height: 100%;
    padding-top: 100px;
}


.field-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    width: 100%;

    & span {
        align-self: flex-start;
        padding: 0 20px;
    }
}

.error-message {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 5px;
    color: red;
    margin: 20px;
    border: 1px dashed red;
    border-radius: 10px;
}