.setting-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  height: 100%;
  padding: 20px 20px;
}

.setting-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  width: 100%;

  & h2 {
    font-size: 16px;
    font-weight: 700;
    align-self: flex-start;
    padding: 0 20px;
  }
}

hr {
  border: 1px solid #939199;
  width: 100%;
}
